@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
body {
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
}

h1, h2, h3 {
  font-weight: 300;
  margin: 30px 0
}

a, a:hover {
  color: inherit;
  text-decoration: none;
}


img {
  width: 100%;
}
.course-card {
    margin-bottom: 40px;
}

.course-card img {
    margin-bottom: 20px;
    height: 80px;
    object-fit: cover;
}

.content-card {
    margin-bottom: 40px;
}

.content-card img {
    height: 80px;
    object-fit: cover;
}
